html {
  font-size: 62.5%; /* 10px */
}
@media screen and ( min-width: 768px ) {
  html {
      font-size: 78.125%; /* 12.5px */
  }
}
body {
  margin: 0;
  font-family: arial;
  font-size: 1.4rem;
  text-decoration: none;
}

  @media only screen and (min-width: 768px) {
  .content:before {
    content: "";
    position: fixed;
    left: 0;
    right: 0;
    z-index: -1;
    
    background-image: url("/images/BramTankink.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    
    -webkit-filter: blur(2px);
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
  }
  
  .content {
    position: fixed;
    left: 0;
    right: 0;
    z-index: 0;
  }
}
div.standardContainer{
  margin-left: 0;
  margin-right: 0;
  padding-top: 5vh;
  background-color: rgba(230,230,240,0.98);
  min-height: 93vh
}

@media only screen and (min-width: 768px) {
  div.standardContainer {
    margin-left: 10vw;
    margin-right: 10vw;
  }
}
div.navbar{
    display: -webkit-flex;
    display: flex;
    margin: 0;
    background-color: #444;
    height: 5vh;
    width: 100vw;
    z-index: 999;
    position: absolute;
    box-shadow: 0px 5px 5px 0px rgba(136,136,136,0.3);
}
div.navbar ul{
    margin-left: auto;
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: inline-block;
}
div.homepageContainer{
    padding: 0px;
    margin: 10vh;
    margin-left: 10vw;
    margin-right: 10vw;
}

div.formsAndMore{
    background-color: rgba(230,225,220,0.98);
    padding: 10px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-style: solid;
    border-top-width: 1vh;
    border-top-color: rgb(70, 130, 190);
    margin-top: -1vh;
    box-shadow: 0px 10px 10px 0px rgba(47, 47, 47, 0.6);
    height: 50vh;
}

button.formTab{
    background-color: rgba(195,190,185,0.98);
    border: none;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    color: #555;
    padding: 5px;
    font-size: 12px;
    font-weight: bold;
    min-height: 4vh;
    min-width: 6vw;
    z-index: 10;
}

button.formTab:hover{
    background-color: rgba(185,180,175,0.98);
}

button.formTab.active{
    background-color: rgba(230,225,220,0.98);
    color: #333;
}

input.form-control{
    background:none;
    border-color: #bbaaaa;
    border-radius: 3px;
    border-style: solid;
    border-width: 1px;
    min-height: 4vh;
    width: 100%;
    min-width: 200px;
    max-width: 300px;
    font-size: 14px;
}

div.inputContainer{
    margin-left: 1vw;
    margin-right: 1vw;
    margin-top: 2vh;
    margin-bottom: 2vh;
}

div.formButtonContainer{
    margin-left: 1vw;
    padding-top: 1vh;
}

button.loginButton{
    border-radius: 6px;
    border-style: none;
    background-color: rgb(70, 130, 190);
    font-weight: bold;
    font-size: 12px;
    height: 35px;
    width: 9vw;
    color: white;
}
tr:nth-child(odd) {
    background: #f9f9f9;
}
tr:nth-child(even) {
    background: #fff;
}

div.stageContainer{  
    height: 100vh;
    overflow: auto;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto auto;
    grid-gap: 1em;
    background-color: rgba(230,230,240,0.98);
    margin-left: 10vw;
    margin-right: 10vw;
    padding-top: 5vh;
    justify-items: center;
}
div#titlebuttons {
    display: grid;
    grid-template-columns: repeat(3,auto);
    grid-template-rows: 1fr;
    grid-gap: 10px;
    grid-column: 1/3;
    grid-row: 1;    
}

div.res {
    grid-column: 1;
    grid-row: 2;
}

div.poule {
    grid-column: 2;
    grid-row: 2;
}

div.stage {
    grid-column: 1;
    grid-row: 3;
}

tr.inteam1{
    color: yellow;
}


th{
    background: rgb(49, 49, 49);
    color: white
    
}

th, td {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
    text-align: center;
    font-size: 17px
}

table {
    border: 1px solid black;
  }

div.teamselectionContainer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    margin-left: 10vw;
    margin-right: 10vw;
    padding-top: 5vh;
    background-color: rgba(230,230,240,0.98);
    min-height: 93vh;
}
div.riderformcontainer{
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 40px;

    padding: 10px;
}
/*De PCS ID form*/
div.classicsSelectionFormContainer{
    grid-column-start: 1;
    grid-row-start: 1;

    padding: 10px;
}
div.pcsForm{
    display: grid;
    grid-template-columns: 4fr 1fr;
    grid-template-rows: 1fr;
}
input.riderFormTextbox{
    grid-column-start: 1;
    grid-row-start: 1;

    background-color: #fff;
    border-color: #bbaaaa;
    border-style: solid;
    border-width: 1px;
    padding-left: 5px;
    width: 98%;
    height: 3vh;
    font-size: 2vh;
}
button.riderSubmitButton{
    grid-column-start: 2;
    grid-row-start: 1;

    border: 0;
    font-size: 10px;
    width: 100%;
    background-color: rgb(70, 130, 190);
}
div.errorDiv{
    grid-column-start: 1;
    grid-row-start: 1;

    background-color: rgb(220,120,120);
    border-radius: 6px;
    margin-top: 10px;
    padding: 10px;
}
table.finances{
    grid-column-start: 1;
    grid-row-start: 2;

    padding: 10px;
}

/*Het kaartje met de rennerinfo*/
div.ridercard{
    grid-column-start: 2;
    grid-row-start: 1;
    grid-row-end: 3;

    display: grid;
    grid-template-columns: 2fr 2fr;
    grid-template-rows: 2fr 100px 40px;

    background-color: rgba(250,250,250);
    font-size: 15px;
    height: 100%;
    margin: 10px;
    padding: 10px;
    width: 80%;
    box-shadow: 0px 10px 10px 0px rgba(136,136,136,0.3);
}
div.ridercard ul{
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;

    list-style-type:none;
    margin-top: 0;
    padding: 0;
    padding-top: 5px;
}
div.ridercard ul li{
    padding-bottom: 5px;
}
img.riderImage{
    grid-column-start: 1;
    grid-row-start: 1;
    grid-row-end: 1;

    width: 100%;
}
select.riderPrice{
    grid-column-start: 2;
    grid-row-start: 1;

    height: 4vh;
    width: 80%;
    margin-left: 10%;
}

button.riderSelectButton{
    grid-row-start: 3;
    grid-column-start: 1;
    grid-column-end: 3;

    background-color: rgb(90, 90, 90);
    border: none;
    font-size: 14px;
    height: 90%;
    margin-bottom: 5%;
}
button.riderSelectButton.active{
    background-color: rgb(70, 130, 190);
}
button.riderSelectButton.active:hover{
    background-color: rgb(90, 150, 210);
}

/*De teamselectietabel*/
div.usertablecontainer{
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
}
table.userTeam{
    width: 90%;
    margin-left: 5%;
}
table.userTeam thead tr th{
    width: 25%
}
table.userTeam tbody tr{
    background-color: rgba(250,250,250);
    box-shadow: 0px 10px 10px 0px rgba(136,136,136,0.3);
    margin: 5px;
}
div.adminpageContainer{
    width: 100%;
    height: 95vh;
    margin-top: 5vh;
    background: #45484d; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #45484d 0%,#000000 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

textarea.queryInputBox{
    font-size: 16px;
    background: black;
    border-radius: 3px;
    border-style: solid;
    border-width: 1px;
    color: white;
}

div.outputTableContainer{
    height: 60vh;
    overflow: auto;
}

table.outputTable{
    font-size: 10px;
    background: black;
    color: white;
}
div.etappewinstenContainer{
    display: grid;
    

    margin-left: 10vw;
    margin-right: 10vw;
    padding-top: 5vh;
    background-color: rgba(230,230,240,0.68);
    
}




div.overzichtContainer{
    margin-left: 10vw;
    margin-right: 10vw;
    padding-top: 5vh;
    background-color: rgba(230,230,240,0.68);
    overflow: auto;
    height: 90vh;
}

table.ScoreTable tr.inteam {
    color: rgb(0, 0, 255);
}

tr:nth-child(odd) {
    background: #a8a8a8;
}
tr:nth-child(even) {
    background: rgb(95, 95, 95);
}

th{
    background: rgb(49, 49, 49);
    color: white
    
}

th, td {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
    text-align: center;
    font-size: 17px
}
div.mainContainer {
    margin-left: 10vw;
    margin-right: 10vw;
    padding-top: 5vh;
    background-color: rgba(230,230,240);
    overflow: auto;
    height: 90vh;
    display:grid;
    grid-template-rows: repeat(3,1fr)    
}

div.iets{
    display:grid;
    grid-template-rows: repeat(3,40px);
    grid-template-columns: repeat(5,1fr)

}

div.row {
    display: grid;
    grid-template-columns: repeat(3, 60px);
    grid-gap: 5px 10px;
    padding: 1em
}

div.grMainRow {
    display: grid;
    grid-template-columns: 35px 60px 40px 60px;
    grid-gap: 5px 10px;
    padding: 1em
}

div.title {
    font-size: 40px;
    grid-column: 1/20
}

div.discription {
    grid-column: 1/5;
    grid-row: auto
}
